<template>
  <div class="login_wrap">
    <div class="login">
      <img src="./img/logo.png" alt="" class="logo">
    </div>
    <div class="show_login">
      <li><span class="iconfont icon-shouji"></span><input v-model="mobile" @blur="mobileReg()"  v-focus type="number" placeholder="请输入手机号"></li>
      <li class="yanzheng"><span class="iconfont icon-yanzhengma"></span><input v-focus v-model="code"   @blur="codeReg()"  type="number" placeholder="请输入验证码">
        <van-button type="default" @click="getCode()" :class="{'sendcode':!isSend,'btn_code':true}" plain>{{codeinfo}}</van-button>
      </li>
      <li><span class="iconfont icon-mima"></span><input v-focus v-model="password" @blur="passwordReg()" type="password" placeholder="6~16位密码，区分大小写"></li>
      <li><span class="iconfont icon-mima"></span><input v-focus v-model="repassword"  @blur="repasswordReg()"  type="password" placeholder="确认密码"></li>
      <li><span class="iconfont icon-yonghu1"></span><input v-focus v-model="name"  id="text"  @blur="nameReg()" type="text" placeholder="姓名"></li>
    </div>
    <van-button  class="btn-login" type="default" @click="register()" plain>注册</van-button>
    <div class="other_link">
      <router-link  to="/user" v-if="!query.shareId">已有账号登录</router-link>
    </div>

  </div>
</template>

<script>
  import { Toast } from 'vant';
  import { regMobile } from "@/common/js/Reg";
  import { apiPost } from "@/common/js/api";
  import axios from 'axios'
 import {  apiGet } from "@/common/js/api";

  export default {
    name: "logout",
    data(){
      let query = this.$route.query;
      return{
        query:query,
        mobilereg:false,
        mobile: '',
        code:'',
        codereg:false,
        password:'',
        passwordreg:false,
        repassword:'',
        repasswordreg:false,
        name:'',
        namereg:'',
        codeback:'',
        codeinfo:'获取验证码',
        isSend: true,
        timer:null
      }
    },
    mounted(){

    },
    methods:{
      mobileReg(){
        let myreg=/^[1][3,4,5,7,8,9][0-9]{9}$/;
        if (!myreg.test(this.mobile)) {
          Toast('请输入正确的手机号');
        }else {
          this.mobilereg=true;
        }
      },
      codeReg(){
        if( this.codeback == this.code){
          this.codereg=true;
        }
      },
      passwordReg(){
          let psdreg=/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/;
          let reg=/^\d{1,}$/
          if(reg.test(this.password)){
            Toast('请不要输入纯数字');
          }else if( !psdreg.test(this.password)){
            Toast('请输入正确格式的密码!');
          } else{
            this.passwordreg=true;
          }
      },
      repasswordReg(){
        if(this.repassword!=this.password){
          Toast('两次密码不一致!');
        }else{
          this.repasswordreg=true;
        }
      },
      nameReg(){

      },
      register(){
        var regZw=/^[\u4E00-\u9FA5]{1,5}$/;
        var regKong  = /^[\s]*$/;
        if (regKong.test(this.name)){
          Toast("姓名不能为空");
        }else if (!regZw.test(this.name)){
          Toast('请输入中文!');
        } else {
          this.namereg =true;
          if(this.mobilereg && this.codereg && this.passwordreg && this.repasswordreg && this.namereg){
            apiPost('/account/register',{cellphone:this.mobile,password: this.password,code:this.code,name:this.name, rcode:this.query.shareId}).then((res)=>{
              console.log(res)
              if(res.data.status==200){
                Toast('注册成功!');
                this.$router.push({path:'/user'})
              }else if(res.data.status==400){
                Toast(res.data.message);
                this.mobile=this.code=this.password=this.repassword=this.name='';
                clearInterval(this.timer)
                this.codeinfo=`获取验证码`
                this.isSend = true
              }
            })
          }else {
            Toast('填入信息有误,请再次确认!');
          }
        }


      },
      getCode() {
        if(regMobile.test(this.mobile) && this.isSend){
          apiGet('/account/sms',{cellphone: this.mobile,type:1}).then((res)=>{
            console.log(res)
            if(res.status==200){
              this.codeback=res.data.data.phonecode;
              console.log(this.codeback)
              Toast(res.data.message);
              if (!this.isSend) {
                return
              }
              this.isSend = false
              let num = 60
              this.codeinfo=`(${num}秒后重试)`
              this.timer = setInterval(() => {
                num --;
                this.codeinfo=`(${num}秒后重试)`
                if (num == 0) {
                  clearInterval(this.timer)
                  this.codeinfo=`获取验证码`
                  this.isSend = true
                }
              }, 1000)
            }else {
              Toast('验证码发送失败,请重试');
            }
          })
        }else if(this.codeinfo != '获取验证码'){
          Toast('验证码发送频繁,请稍后再试');
        }
        else{
          Toast('请输入正确格式的手机号!');
        }
         /*axios.get('/api/menu',{params:{restaurant_id: 155147,terminal:'web'}}).then((res)=>{
           console.log(res)
         })*/
      }
    }
  }
</script>

<style scoped lang="scss">
  .login{
    height: 2.17rem;
    background:linear-gradient(-90deg,rgba(70,133,238,1),rgba(127,173,249,1));
    overflow: hidden;
    position: relative;
    .logo{
      width: 2.25rem;
      height: 0.46rem;
      display: block;
      margin: 0.78rem auto 0;
    }
    .tab_login{
      position: absolute;
      display: flex;
      width: 100%;
      height: 0.4rem;
      color: rgba(255,255,255,.5);
      text-align: center;
      font-weight: 400;
      font-size:0.16rem;
      bottom: 0;
      i{font-style: normal}
    }

  }
  .zhang_login,.show_login{
    margin-top: 0.65rem;
    li{
      list-style: none;
      position: relative;
      width: 3.06rem;
      height: 0.32rem;
      color: #9a9a9a;
      border-bottom: 1px solid #f5f5f5;
      margin: 0 auto .35rem;
      span{
        float: left;
        font-size: 0.2rem;
        margin: 0 0.08rem;
      }
      input{
        font-size: .17rem;
        float: left;
        border: none;
        height: .3rem;
        width: 2.6rem;
      }
      img{
        width: 0.22rem;
        border-radius: 0.22rem;
        right: 0.03rem;
        height: 0.22rem;
        position: absolute;
      }
    }
    .yanzheng{
      position: relative;
      .btn_code{
        width:0.94rem;
        position: absolute;
        right: 0;
        background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
        border-radius:0.04rem;
        color: white;
        font-size: .14rem;
        padding:0;
        line-height: 0.31rem;
        height: 0.31rem;
        bottom: 0.1rem;
        border: none;
      }
      .sendcode{
        background:linear-gradient(-90deg,rgba(178,178,178,1),rgba(201,201,201,1));
        border-radius:0.04rem;
        color: white;
      }
      img{
        right: 1rem;
      }
    }
    .focus{
      border-bottom: 1px solid #3C7FEE;
      color: #333;
      span{
        color: #3C7FEE;
      }
    }
  }
  .btn-login{
    width: 3.06rem;
    height: .47rem;
    background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
    border-radius:0.04rem;
    font-size: 0.17rem;
    text-align: center;
    line-height: .47rem;
    outline: none;
    border: none;
    display: block;
    margin: 0 auto;
    color: white;
  }
  .other_link{
    width: 3.06rem;
    overflow: hidden;
    margin: .36rem auto ;
    a{font-size: .16rem; color: #9a9a9a;}
    a:nth-of-type(1){float: left;}
    a:nth-of-type(2){float: right;}
  }

</style>



